import React from 'react';

class ReduxCounter extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div>
      <p>{props.count}</p>
      <button  onClick={props.decrement}>-</button>
      <button onClick={props.increment}>+</button>
      </div>
    )
  }
}

export default ReduxCounter